import StateArrayModel from "../../../../wrappers/StateModels/StateArrayModel";
import { simulateLogic } from "./logic";

const Experience = new StateArrayModel({ stateName: "experience" });

Experience.createSlice();

export const { stateName, reducer } = Experience.getEntity();

export const { Add, Fetch, Edit, Remove, Patch } = Experience.getAPIHandles();

Experience.enableCustomAct((dispatch, action) => {
  const response = Experience.getAPICallType(action.payload.onSuccess);
  if (response) {
    const { type, apiType } = response;
    simulateLogic({
      type,
      apiType,
      dispatch,
      action,
    });
  }
});

export const {
  selectAddStatus,
  selectFetchStatus,
  selectEditStatus,
  selectDeleteStatus,
  selectPatchStatus,
  selectData,
} = Experience.getSelectors();

export { selectData as selectExperiences };
