import StateArrayModel from "../../../../wrappers/StateModels/StateArrayModel";
import { simulateLogic } from "./logic";

const LeaveBalance = new StateArrayModel({ stateName: "leave_bslance" });

LeaveBalance.createSlice();

export const { stateName, reducer } = LeaveBalance.getEntity();

export const { Add, Fetch, Edit, Remove, Patch } = LeaveBalance.getAPIHandles();

LeaveBalance.enableCustomAct((dispatch, action) => {
  const response = LeaveBalance.getAPICallType(action.payload.onSuccess);
  if (response) {
    const { type, apiType } = response;
    simulateLogic({
      type,
      apiType,
      dispatch,
      action,
    });
  }
});

export const {
  selectAddStatus,
  selectFetchStatus,
  selectEditStatus,
  selectDeleteStatus,
  selectPatchStatus,
  selectData,
} = LeaveBalance.getSelectors();

export { selectData as selectLeaveBalances };
