import StateArrayModel from "../../../../wrappers/StateModels/StateArrayModel";
import { simulateLogic } from "./logic";

const EducationType = new StateArrayModel({
  stateName: "education_type",
});
EducationType.setURL("/education-type");
EducationType.createSlice();

export const { stateName, reducer } = EducationType.getEntity();

EducationType.enableCustomAct((dispatch, action) => {
  const response = EducationType.getAPICallType(action.payload.onSuccess);
  if (response) {
    const { type, apiType } = response;
    simulateLogic({
      type,
      apiType,
      dispatch,
      action,
    });
  }
});

EducationType.enableResults({ fetchEnabled: false });

EducationType.toggleCustomAct(true);
export const {
  Add,
  Fetch,
  Edit,
  Remove,
  Patch,
} = EducationType.getAPIHandles();
export const {
  selectAddStatus,
  selectFetchStatus,
  selectEditStatus,
  selectDeleteStatus,
  selectData,
} = EducationType.getSelectors();

export { selectData as selectEducationTypes };
