import StateArrayModel from "../../../../wrappers/StateModels/StateArrayModel";
import { simulateLogic } from "./logic";

const DisciplinaryAction = new StateArrayModel({
  stateName: "disciplinary_action",
});

DisciplinaryAction.createSlice();

export const { stateName, reducer } = DisciplinaryAction.getEntity();

export const {
  Add,
  Fetch,
  Edit,
  Remove,
  Patch,
} = DisciplinaryAction.getAPIHandles();

DisciplinaryAction.enableCustomAct((dispatch, action) => {
  const response = DisciplinaryAction.getAPICallType(action.payload.onSuccess);
  if (response) {
    const { type, apiType } = response;
    simulateLogic({
      type,
      apiType,
      dispatch,
      action,
    });
  }
});

export const {
  selectAddStatus,
  selectFetchStatus,
  selectEditStatus,
  selectDeleteStatus,
  selectPatchStatus,
  selectData,
} = DisciplinaryAction.getSelectors();

export { selectData as selectDisciplinaryActions };
