import StateArrayModel from "../../../../wrappers/StateModels/StateArrayModel";
import { simulateLogic } from "../logic";

const Certification = new StateArrayModel({ stateName: "certification" });

Certification.createSlice();

export const { stateName, reducer } = Certification.getEntity();

export const {
  Add,
  Fetch,
  Edit,
  Remove,
  Patch,
} = Certification.getAPIHandles();

Certification.enableCustomAct((dispatch, action) => {
  const response = Certification.getAPICallType(action.payload.onSuccess);
  if (response) {
    const { type, apiType } = response;
    simulateLogic({
      type,
      apiType,
      dispatch,
      action,
    });
  }
});

export const {
  selectAddStatus,
  selectFetchStatus,
  selectEditStatus,
  selectDeleteStatus,
  selectPatchStatus,
  selectData,
} = Certification.getSelectors();

export { selectData as selectCertifications };
