import StateArrayModel from "../../../../wrappers/StateModels/StateArrayModel";
import { simulateLogic } from "./logic";

const CertificationType = new StateArrayModel({
  stateName: "certification_type",
});
CertificationType.setURL("/certification-type");
CertificationType.createSlice();

export const { stateName, reducer } = CertificationType.getEntity();

CertificationType.enableCustomAct((dispatch, action) => {
  const response = CertificationType.getAPICallType(action.payload.onSuccess);
  if (response) {
    const { type, apiType } = response;
    simulateLogic({
      type,
      apiType,
      dispatch,
      action,
    });
  }
});

CertificationType.enableResults({ fetchEnabled: false });

CertificationType.toggleCustomAct(true);
export const {
  Add,
  Fetch,
  Edit,
  Remove,
  Patch,
} = CertificationType.getAPIHandles();
export const {
  selectAddStatus,
  selectFetchStatus,
  selectEditStatus,
  selectDeleteStatus,
  selectData,
} = CertificationType.getSelectors();

export { selectData as selectCertificationTypes };
