import StateArrayModel from "../../../../wrappers/StateModels/StateArrayModel";
import { simulateLogic } from "../logic";

const Termination = new StateArrayModel({ stateName: "training" });

Termination.createSlice();

export const { stateName, reducer } = Termination.getEntity();

export const { Add, Fetch, Edit, Remove, Patch } = Termination.getAPIHandles();

Termination.enableCustomAct((dispatch, action) => {
  const response = Termination.getAPICallType(action.payload.onSuccess);
  if (response) {
    const { type, apiType } = response;
    simulateLogic({
      type,
      apiType,
      dispatch,
      action,
    });
  }
});

export const {
  selectAddStatus,
  selectFetchStatus,
  selectEditStatus,
  selectDeleteStatus,
  selectPatchStatus,
  selectData,
} = Termination.getSelectors();

export { selectData as selectTerminations };
