import StateArrayModel from "../../../../wrappers/StateModels/StateArrayModel";
import { simulateLogic } from "./logic";

const AbsenceRegistration = new StateArrayModel({
  stateName: "absence_registration",
});

AbsenceRegistration.createSlice();

export const { stateName, reducer } = AbsenceRegistration.getEntity();

export const {
  Add,
  Fetch,
  Edit,
  Remove,
  Patch,
} = AbsenceRegistration.getAPIHandles();

AbsenceRegistration.enableCustomAct((dispatch, action) => {
  const response = AbsenceRegistration.getAPICallType(action.payload.onSuccess);
  if (response) {
    const { type, apiType } = response;
    simulateLogic({
      type,
      apiType,
      dispatch,
      action,
    });
  }
});

export const {
  selectAddStatus,
  selectFetchStatus,
  selectEditStatus,
  selectDeleteStatus,
  selectPatchStatus,
  selectData,
} = AbsenceRegistration.getSelectors();

export { selectData as selectAbsenceRegistrations };
