import StateArrayModel from "../../../../wrappers/StateModels/StateArrayModel";
import { simulateLogic } from "./logic";

const LeaveType = new StateArrayModel({
  stateName: "leave_type",
});
LeaveType.setURL("/leave-type");
LeaveType.createSlice();

export const { stateName, reducer } = LeaveType.getEntity();

LeaveType.enableCustomAct((dispatch, action) => {
  const response = LeaveType.getAPICallType(action.payload.onSuccess);
  if (response) {
    const { type, apiType } = response;
    simulateLogic({
      type,
      apiType,
      dispatch,
      action,
    });
  }
});

LeaveType.enableResults({ fetchEnabled: false });

LeaveType.toggleCustomAct(true);
export const { Add, Fetch, Edit, Remove, Patch } = LeaveType.getAPIHandles();
export const {
  selectAddStatus,
  selectFetchStatus,
  selectEditStatus,
  selectDeleteStatus,
  selectData,
} = LeaveType.getSelectors();

export { selectData as selectLeaveTypes };
