import StateArrayModel from "../../../../wrappers/StateModels/StateArrayModel";
import { simulateLogic } from "./logic";

const Institution = new StateArrayModel({
  stateName: "institution",
});
Institution.setURL("/institution");
Institution.createSlice();

export const { stateName, reducer } = Institution.getEntity();

Institution.enableCustomAct((dispatch, action) => {
  const response = Institution.getAPICallType(action.payload.onSuccess);
  if (response) {
    const { type, apiType } = response;
    simulateLogic({
      type,
      apiType,
      dispatch,
      action,
    });
  }
});

Institution.enableResults({ fetchEnabled: false });

Institution.toggleCustomAct(true);
export const { Add, Fetch, Edit, Remove, Patch } = Institution.getAPIHandles();
export const {
  selectAddStatus,
  selectFetchStatus,
  selectEditStatus,
  selectDeleteStatus,
  selectData,
} = Institution.getSelectors();

export { selectData as selectInstitutions };
