import StateArrayModel from "../../../../wrappers/StateModels/StateArrayModel";
import { simulateLogic } from "./logic";

const Performance = new StateArrayModel({ stateName: "performance" });

Performance.createSlice();

export const { stateName, reducer } = Performance.getEntity();

export const { Add, Fetch, Edit, Remove, Patch } = Performance.getAPIHandles();

Performance.enableCustomAct((dispatch, action) => {
  const response = Performance.getAPICallType(action.payload.onSuccess);
  if (response) {
    const { type, apiType } = response;
    simulateLogic({
      type,
      apiType,
      dispatch,
      action,
    });
  }
});

export const {
  selectAddStatus,
  selectFetchStatus,
  selectEditStatus,
  selectDeleteStatus,
  selectPatchStatus,
  selectData,
} = Performance.getSelectors();

export { selectData as selectPerformances };
