import StateArrayModel from "../../../../wrappers/StateModels/StateArrayModel";
import { simulateLogic } from "./logic";

const TrainingType = new StateArrayModel({
  stateName: "training_type",
});
TrainingType.setURL("/training-type");
TrainingType.createSlice();

export const { stateName, reducer } = TrainingType.getEntity();

TrainingType.enableCustomAct((dispatch, action) => {
  const response = TrainingType.getAPICallType(action.payload.onSuccess);
  if (response) {
    const { type, apiType } = response;
    simulateLogic({
      type,
      apiType,
      dispatch,
      action,
    });
  }
});

TrainingType.enableResults({ fetchEnabled: false });

TrainingType.toggleCustomAct(true);
export const { Add, Fetch, Edit, Remove, Patch } = TrainingType.getAPIHandles();
export const {
  selectAddStatus,
  selectFetchStatus,
  selectEditStatus,
  selectDeleteStatus,
  selectData,
} = TrainingType.getSelectors();

export { selectData as selectTrainingTypes };
