import StateArrayModel from "../../../../wrappers/StateModels/StateArrayModel";
import { simulateLogic } from "./logic";

const DisciplinaryActionType = new StateArrayModel({
  stateName: "disciplinary_action_type",
});
DisciplinaryActionType.setURL("/disciplinary_action-type");
DisciplinaryActionType.createSlice();

export const { stateName, reducer } = DisciplinaryActionType.getEntity();

DisciplinaryActionType.enableCustomAct((dispatch, action) => {
  const response = DisciplinaryActionType.getAPICallType(
    action.payload.onSuccess
  );
  if (response) {
    const { type, apiType } = response;
    simulateLogic({
      type,
      apiType,
      dispatch,
      action,
    });
  }
});

DisciplinaryActionType.enableResults({ fetchEnabled: false });

DisciplinaryActionType.toggleCustomAct(true);
export const {
  Add,
  Fetch,
  Edit,
  Remove,
  Patch,
} = DisciplinaryActionType.getAPIHandles();
export const {
  selectAddStatus,
  selectFetchStatus,
  selectEditStatus,
  selectDeleteStatus,
  selectData,
} = DisciplinaryActionType.getSelectors();

export { selectData as selectDisciplinaryActionTypes };
