import StateArrayModel from "../../../../wrappers/StateModels/StateArrayModel";
import { simulateLogic } from "../logic";

const Training = new StateArrayModel({ stateName: "training" });

Training.createSlice();

export const { stateName, reducer } = Training.getEntity();

export const { Add, Fetch, Edit, Remove, Patch } = Training.getAPIHandles();

Training.enableCustomAct((dispatch, action) => {
  const response = Training.getAPICallType(action.payload.onSuccess);
  if (response) {
    const { type, apiType } = response;
    simulateLogic({
      type,
      apiType,
      dispatch,
      action,
    });
  }
});

export const {
  selectAddStatus,
  selectFetchStatus,
  selectEditStatus,
  selectDeleteStatus,
  selectPatchStatus,
  selectData,
} = Training.getSelectors();

export { selectData as selectTrainings };
