import StateArrayModel from "../../../../wrappers/StateModels/StateArrayModel";
import { simulateLogic } from "./logic";

const TerminationType = new StateArrayModel({
  stateName: "termination_type",
});
TerminationType.setURL("/termination-type");
TerminationType.createSlice();

export const { stateName, reducer } = TerminationType.getEntity();

TerminationType.enableCustomAct((dispatch, action) => {
  const response = TerminationType.getAPICallType(action.payload.onSuccess);
  if (response) {
    const { type, apiType } = response;
    simulateLogic({
      type,
      apiType,
      dispatch,
      action,
    });
  }
});

TerminationType.enableResults({ fetchEnabled: false });

TerminationType.toggleCustomAct(true);
export const {
  Add,
  Fetch,
  Edit,
  Remove,
  Patch,
} = TerminationType.getAPIHandles();
export const {
  selectAddStatus,
  selectFetchStatus,
  selectEditStatus,
  selectDeleteStatus,
  selectData,
} = TerminationType.getSelectors();

export { selectData as selectTerminationTypes };
