import StateArrayModel from "../../../../wrappers/StateModels/StateArrayModel";
import { simulateLogic } from "./logic";

const Consignment = new StateArrayModel({ stateName: "consignment" });

Consignment.createSlice();

export const { stateName, reducer } = Consignment.getEntity();

export const { Add, Fetch, Edit, Remove, Patch } = Consignment.getAPIHandles();

Consignment.enableCustomAct((dispatch, action) => {
  const response = Consignment.getAPICallType(action.payload.onSuccess);
  if (response) {
    const { type, apiType } = response;
    simulateLogic({
      type,
      apiType,
      dispatch,
      action,
    });
  }
});

export const {
  selectAddStatus,
  selectFetchStatus,
  selectEditStatus,
  selectDeleteStatus,
  selectPatchStatus,
  selectData,
} = Consignment.getSelectors();

export { selectData as selectConsignments };
