import StateArrayModel from "../../../../wrappers/StateModels/StateArrayModel";
import { simulateLogic } from "./logic";

const Qualification = new StateArrayModel({ stateName: "qualification" });

Qualification.createSlice();

export const { stateName, reducer } = Qualification.getEntity();

export const {
  Add,
  Fetch,
  Edit,
  Remove,
  Patch,
} = Qualification.getAPIHandles();

Qualification.enableCustomAct((dispatch, action) => {
  const response = Qualification.getAPICallType(action.payload.onSuccess);
  if (response) {
    const { type, apiType } = response;
    simulateLogic({
      type,
      apiType,
      dispatch,
      action,
    });
  }
});

export const {
  selectAddStatus,
  selectFetchStatus,
  selectEditStatus,
  selectDeleteStatus,
  selectPatchStatus,
  selectData,
} = Qualification.getSelectors();

export { selectData as selectQualifications };
